import React from "react";
import MatchEntity from "../../../../../../../../model/match-entity";
import Analytic from "./analytic/Analytic";
import GameTypeCategory from "./game-type-category/GameTypeCategory";
import Match from "./match/Match";
interface Props {
  match: MatchEntity[];
}

function Matches(props: Props) {
  return (
    <div className="flex flex-col gap-5">
      <GameTypeCategory />
      <Analytic />
      <div className="flex flex-col gap-1">
        {props.match?.map((match) => (
          <Match key={match._id?.$oid} matchId={match} />
        ))}
      </div>
    </div>
  );
}

export default Matches;
