import { useParams } from "@reach/router";
import React, { useEffect, useState } from "react";
import GameRate, { mode } from "../components/summoner/game-rate/GameRate";
import TierGraph from "../components/summoner/graph/TierGraph";
import Matches from "../components/summoner/matches/Matches";
import Profile from "../components/summoner/profile/Profile";
import Rank, { type } from "../components/summoner/profile/Rank";
import UserData from "../../../../../../data/potg/Users.json";
import MatchData1 from "../../../../../../data/potg/matches/matches1.json";
import MatchData2 from "../../../../../../data/potg/matches/matches2.json";
import MatchData3 from "../../../../../../data/potg/matches/matches3.json";
import MatchEntity from "../../../../../../model/match-entity";

interface SummonerProfile {
  name: string | null | undefined;
  icon: number | null | undefined;
  level: number | null | undefined;
  id: string | null | undefined;
  puuid: string | null | undefined;
  lastUpdate: number;
}

interface SummonerLeague {
  tier: number | null | undefined;
  rank: number | null | undefined;
  leaguePoints: number | null | undefined;

  wins: number | null | undefined;
  losses: number | null | undefined;
}

interface SummonerData {
  profile: SummonerProfile;
  leagueSolo: SummonerLeague;
  leagueFlex: SummonerLeague;
  matches: MatchEntity[] | null;
}

const Summoner = () => {
  const param = useParams();

  const [summonerData, setSummonerData] = useState<SummonerData | null>();
  // const [matchesData, setMatchesData] = useState<MatchEntity[]>([]);

  useEffect(() => {
    const userData = UserData.find((data) => {
      if (data.name === param.name) return data;
    });

    let matches: MatchEntity[] = [];

    switch (param.name) {
      case "우월한기럭지":
        matches = MatchData1;
        break;
      case "Hide on bush":
        matches = MatchData2;
        break;
      case "섹 시 본 좌":
        matches = MatchData3;
        break;
    }

    setSummonerData({
      profile: {
        name: userData?.name,
        icon: userData?.profileIconId,
        level: userData?.level,
        id: userData?.accountId,
        puuid: userData?.puuid,
        lastUpdate: 0,
      },
      leagueSolo: {
        tier: userData?.ranks.at(0)?.tier,
        rank: userData?.ranks.at(0)?.rank,
        leaguePoints: userData?.ranks.at(0)?.leaguePoints,
        wins: userData?.ranks.at(0)?.wins,
        losses: userData?.ranks.at(0)?.losses,
      },
      leagueFlex: {
        tier: userData?.ranks.at(1)?.tier,
        rank: userData?.ranks.at(1)?.rank,
        leaguePoints: userData?.ranks.at(1)?.leaguePoints,
        wins: userData?.ranks.at(1)?.wins,
        losses: userData?.ranks.at(1)?.losses,
      },
      matches: matches,
    });
  }, []);

  return (
    <div className="flex flex-col gap-8">
      {/* box-shadow: 0px 0px 30px rgb(242, 242, 242); */}
      <div className="flex gap-5">
        <Profile
          name={summonerData?.profile.name}
          level={summonerData?.profile.level}
          icon={summonerData?.profile.icon}
        />
        <TierGraph />
      </div>
      <div className="flex flex-row gap-14 ml-8 items-center">
        <div className="flex flex-row gap-16">
          <Rank
            type={type.solo}
            win={summonerData?.leagueSolo.wins}
            loss={summonerData?.leagueSolo.losses}
            rank={summonerData?.leagueSolo.rank}
            point={summonerData?.leagueSolo.leaguePoints}
            tier={summonerData?.leagueSolo.tier}
          />
          <Rank
            type={type.flex}
            win={summonerData?.leagueFlex.wins}
            loss={summonerData?.leagueFlex.losses}
            rank={summonerData?.leagueFlex.rank}
            point={summonerData?.leagueFlex.leaguePoints}
            tier={summonerData?.leagueFlex.tier}
          />
        </div>
        <div className="flex flex-col gap-4">
          <GameRate type={mode.SUMMONERS_RIFT} win={213} lose={142} />
          <div className="border-t border-[#d8d8d8]" />
          <GameRate type={mode.ARAM} win={132} lose={52} />
        </div>
      </div>
      <div className="border-t border-[#d8d8d8]" />
      <div className="flex flex-col gap-4 ml-10 text-gray-500">
        <div className="flex gap-1 ml-5 font-bold">
          <div>최근</div>
          <div className="text-orange-500">20게임</div>
          <div>전적</div>
        </div>
        <div>
          <Matches match={summonerData?.matches || []} />
        </div>
      </div>
    </div>
  );
};

export default Summoner;
